<template>
    <div class="procss">
        <el-steps :active="activeStep" finish-status="success" align-center>
            <el-step title="申请人"></el-step>
            <el-step title="被申请人"></el-step>
            <el-step title="纠纷原因"></el-step>
            <el-step title="添加证据、诉求"></el-step>
            <el-step title="调解信息"></el-step>
        </el-steps>
        <div class="stepOne" v-if="stepOne">
            <el-form :model="stepForm" ref="stepForm" label-width="100px" class="demo-ruleForm" :label-position="labelPosition">
                <div class="oneLft">
                    <el-form-item label="身份类型" prop="identity" hide-required-asterisk>
                        <el-select v-model="stepForm.identity" clearable placeholder="请选择">
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="申请人姓名" prop="applicant" hide-required-asterisk>
                        <el-input placeholder="请输入申请人姓名" v-model="stepForm.applicant" clearable> </el-input>
                    </el-form-item>
                    <el-form-item label="证件类型" prop="certificates" hide-required-asterisk>
                        <el-select v-model="stepForm.certificates" clearable placeholder="请选择">
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="证件号码" prop="card" hide-required-asterisk>
                        <el-input placeholder="请输入证件号码" v-model="stepForm.applicant" clearable> </el-input>
                    </el-form-item>
                    <el-form-item label="性别" prop="sex" hide-required-asterisk>
                        <el-radio-group v-model="stepForm.sex">
                            <el-radio :label="3">男</el-radio>
                            <el-radio :label="6">女</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="出生日期" prop="date" hide-required-asterisk>
                        <el-date-picker v-model="stepForm.date" type="date" placeholder="选择日期"> </el-date-picker>
                    </el-form-item>
                    <el-form-item label="电话号码" prop="phone" hide-required-asterisk>
                        <el-input placeholder="请输入电话号码" v-model="stepForm.phone" clearable> </el-input>
                    </el-form-item>
                    <el-form-item label="居住地" prop="place" hide-required-asterisk>
                        <el-input placeholder="请输入居住地" v-model="stepForm.place" clearable> </el-input>
                    </el-form-item>
                    <el-form-item label="民族" prop="nation" hide-required-asterisk>
                        <el-input placeholder="请输入" v-model="stepForm.nation" clearable> </el-input>
                    </el-form-item>
                    <el-form-item label="户籍所在地" prop="Household" hide-required-asterisk>
                        <el-input placeholder="请输入户籍所在地" v-model="stepForm.Household" clearable> </el-input>
                    </el-form-item>
                    <el-form-item label="与患者关系" prop="relationship" hide-required-asterisk>
                        <el-select v-model="stepForm.relationship"  @change='get()' clearable placeholder="请选择">
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </div>
                <div class="oneRight">
                    <p>证件上传<span>(必须上传)</span></p>
                    <div class="uploadBox">
                        <el-upload
                            class="avatar-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :show-file-list="false"
                            :on-success="handleAvatarSuccess"
                            :before-upload="beforeAvatarUpload">
                            <img v-if="imageUrl" :src="imageUrl" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                        <p>证件身份证人像面</p>
                    </div>
                    <div class="uploadBoxt">
                        <el-upload
                            class="avatar-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :show-file-list="false"
                            :on-success="handleAvatarSuccess"
                            :before-upload="beforeAvatarUpload">
                            <img v-if="imageUrl" :src="imageUrl" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                        <p>证件身份证国徽面</p>
                    </div>
                    <div class="uploadBox">
                        <el-upload
                            class="avatar-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :show-file-list="false"
                            :on-success="handleAvatarSuccess"
                            :before-upload="beforeAvatarUpload">
                            <img v-if="imageUrl" :src="imageUrl" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                        <p>授权委托书</p>
                    </div>
                    <div class="uploadBoxt">
                        <el-upload
                            class="avatar-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :show-file-list="false"
                            :on-success="handleAvatarSuccess"
                            :before-upload="beforeAvatarUpload">
                            <img v-if="imageUrl" :src="imageUrl" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                        <p>受理证明</p>
                    </div>
                </div>
                <div style="clear:both;"></div>
                <div class="huanzhe" v-if="isHuanzhe">
                    <div class="oneLft">
                        <el-form-item label="患者姓名" prop="applicant" hide-required-asterisk>
                            <el-input placeholder="请输入患者姓名" v-model="stepForm.applicant" clearable> </el-input>
                        </el-form-item>
                        <el-form-item label="性别" prop="sex" hide-required-asterisk>
                            <el-radio-group v-model="stepForm.sex">
                                <el-radio :label="3">男</el-radio>
                                <el-radio :label="6">女</el-radio>
                            </el-radio-group>
                        </el-form-item>
                        <el-form-item label="所属城市" prop="place" hide-required-asterisk>
                            <el-input placeholder="请输入" v-model="stepForm.place" clearable> </el-input>
                        </el-form-item>
                        <el-form-item label="电话号码" prop="phone" hide-required-asterisk>
                            <el-input placeholder="请输入电话号码" v-model="stepForm.phone" clearable> </el-input>
                        </el-form-item>
                        <el-form-item label="与患者关系" prop="relationship" hide-required-asterisk>
                            <el-select v-model="stepForm.relationship" clearable placeholder="请选择">
                                <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="所患疾病" prop="remake" hide-required-asterisk>
                            <el-input type="textarea" placeholder="所患疾病" v-model="stepForm.remake" clearable> </el-input>
                        </el-form-item>
                    </div>
                    <div class="oneRight">
                        <p>证件上传<span>(必须上传)</span></p>
                        <div class="uploadBox">
                            <el-upload
                                class="avatar-uploader"
                                action="https://jsonplaceholder.typicode.com/posts/"
                                :show-file-list="false"
                                :on-success="handleAvatarSuccess"
                                :before-upload="beforeAvatarUpload">
                                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                            <p>证件身份证人像面</p>
                        </div>
                        <div class="uploadBoxt">
                            <el-upload
                                class="avatar-uploader"
                                action="https://jsonplaceholder.typicode.com/posts/"
                                :show-file-list="false"
                                :on-success="handleAvatarSuccess"
                                :before-upload="beforeAvatarUpload">
                                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                            <p>证件身份证国徽面</p>
                        </div>
                    </div>
                </div>
                <div style="clear:both;"></div>
                <div class="stepBottom">
                    <el-button>保存</el-button>
                    <el-button type="primary" @click="nextStep('1')">下一步</el-button>
                </div>
            </el-form>
        </div>
        <div class="stepOne" v-if="stepTwo">
            <el-form :model="stepForm" ref="stepForm" label-width="100px" class="demo-ruleForm" :label-position="labelPosition">
                <div class="twoLeft">
                    <el-form-item label="身份类型" prop="identity" hide-required-asterisk>
                        <el-select v-model="stepForm.identity" clearable placeholder="请选择">
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="被申请人姓名" prop="applicant" hide-required-asterisk>
                        <el-input placeholder="请输入" v-model="stepForm.applicant" clearable> </el-input>
                    </el-form-item>
                    <el-form-item label="组织名称" prop="card" hide-required-asterisk>
                        <el-input placeholder="请输入" v-model="stepForm.applicant" clearable> </el-input>
                    </el-form-item>
                    <el-form-item label="涉及科室" prop="phone" hide-required-asterisk>
                        <el-input placeholder="请输入电话号码" v-model="stepForm.phone" clearable> </el-input>
                    </el-form-item>
                    <el-form-item label="调解类别" prop="place" hide-required-asterisk>
                        <el-input placeholder="请输入居住地" v-model="stepForm.place" clearable> </el-input>
                    </el-form-item>
                    <el-form-item label="与患者关系" prop="certificates" hide-required-asterisk>
                        <el-select v-model="stepForm.certificates" clearable placeholder="请选择">
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </div>
                <div class="stepBottom">
                    <el-button type="primary" @click="backStep('2')">上一步</el-button>
                    <el-button>保存</el-button>
                    <el-button type="primary" @click="nextStep('2')">下一步</el-button>
                </div>
            </el-form>
        </div>
        <div class="stepOne" v-if="stepSan">
            <el-form :model="stepForm" ref="stepForm" label-width="100px" class="demo-ruleForm" :label-position="labelPosition">
                <div class="twoLeft">
                    <el-form-item label="被申请人姓名" prop="applicant" hide-required-asterisk>
                        <el-input type="textarea" :rows="5" placeholder="请输入" v-model="stepForm.applicant" clearable> </el-input>
                    </el-form-item>
                </div>
                <div class="stepBottom">
                    <el-button type="primary" @click="backStep('3')">上一步</el-button>
                    <el-button>保存</el-button>
                    <el-button type="primary" @click="nextStep('3')">下一步</el-button>
                </div>
            </el-form>
        </div>
        <div class="stepOne" v-if="stepSi">
            <el-form :model="stepForm" ref="stepForm" label-width="100px" class="demo-ruleForm" :label-position="labelPosition">
                <div class="stepSi">
                    <el-form-item label="诉求" prop="applicant" hide-required-asterisk>
                        <el-input style="width:50%;float: left" placeholder="请输入" v-model="stepForm.applicant" clearable> </el-input>
                    </el-form-item>
                    <el-form-item v-for="(item, index) in ListType" :key="index" :label="item" prop="applicant" hide-required-asterisk>
                        <el-upload
                            style="float: left"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            list-type="picture-card"
                            :on-preview="handlePictureCardPreview"
                            :on-remove="handleRemove">
                            <i class="el-icon-plus"></i>
                        </el-upload>
                        <el-dialog :visible.sync="dialogVisible">
                            <img width="100%" :src="dialogImageUrl" alt="">
                        </el-dialog>
                    </el-form-item>
                </div>
                <div class="stepBottom">
                    <el-button type="primary" @click="backStep('4')">上一步</el-button>
                    <el-button>保存</el-button>
                    <el-button type="primary" @click="nextStep('4')">下一步</el-button>
                </div>
            </el-form>
        </div>
        <div class="stepOne" v-if="stepWu">
            <el-form :model="stepForm" ref="stepForm" label-width="100px" class="demo-ruleForm" :label-position="labelPosition">
                <div class="stepSi">
                    <el-form-item label="调解补充" prop="applicant" hide-required-asterisk>
                        <el-upload
                            class="upload-demo"
                            drag
                            style="float: left"
                            :file-list="fileList"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            multiple>
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                    </el-form-item>
                </div>
                <div class="stepBottom">
                    <el-button type="primary" @click="backStep('5')">上一步</el-button>
                    <el-button>保存</el-button>
                    <el-button type="primary">提交</el-button>
                </div>
            </el-form>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return{
            activeStep: 0,
            labelPosition: 'right',
            stepForm: {},
            options: [
                {
                    value: '1',
                    label: '个人'
                }, {
                    value: '2',
                    label: '其他组织'
                }
            ],
            imageUrl: '',
            isHuanzhe: false,
            stepOne: true,
            stepTwo: false,
            stepSan: false,
            stepSi: false,
            stepWu: false,
            dialogImageUrl: '',
            fileList: [],
            dialogVisible: false,
            ListType: ['医疗纠纷材料','授权委托书', '委托人、被委托人相关证件','病例及相关检查资料','医疗费用及其他相关费用票据','误工证明','患者死亡相关证明','新生儿出生证明']
        }
    },
    methods: {
        handleAvatarSuccess(res, file) {
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload(file) {
            console.log(file)
            const isJPG = file.type === 'image/jpeg';
            const isGIF = file.type === 'image/gif';
            const isJPEG = file.type === 'image/jpeg';
            const isPNG = file.type === 'image/png';
            const isBMP = file.type === 'image/bmp';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG && !isGIF && !isJPEG && !isPNG && !isBMP) {
                this.$message.error('上传图片只能是 gif,jpg,jpeg,png,bmp 格式!');
            }
            if (!isLt2M) {
                this.$message.error('上传图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
        },
        get() {
            if (this.stepForm.relationship == '1') {
                this.isHuanzhe = true
            } else {
                this.isHuanzhe = false
            }
        },
        nextStep(item) {
            if (item == 1) {
                this.stepOne = false
                this.stepTwo = true
                this.stepSan = false
                this.stepSi = false
                this.stepWu = false
                this.activeStep++
            } else if (item == 2) {
                this.stepOne = false
                this.stepTwo = false
                this.stepSan = true
                this.stepSi = false
                this.stepWu = false
                this.activeStep++
            } else if (item == 3) {
                this.stepOne = false
                this.stepTwo = false
                this.stepSan = false
                this.stepSi = true
                this.stepWu = false
                this.activeStep++
            } else if (item == 4) {
                this.stepOne = false
                this.stepTwo = false
                this.stepSan = false
                this.stepSi = false
                this.stepWu = true
                this.activeStep++
            }
        },
        backStep(item) {
            if (item == 2) {
                this.stepOne = true
                this.stepTwo = false
                this.stepSan = false
                this.stepSi = false
                this.stepWu = false
                this.activeStep--
            } else if (item == 3) {
                this.stepOne = false
                this.stepTwo = true
                this.stepSan = false
                this.stepSi = false
                this.stepWu = false
                this.activeStep--
            } else if (item == 4) {
                this.stepOne = false
                this.stepTwo = false
                this.stepSan = true
                this.stepSi = false
                this.stepWu = false
                this.activeStep--
            } else if (item == 5) {
                this.stepOne = false
                this.stepTwo = false
                this.stepSan = false
                this.stepSi = true
                this.stepWu = false
                this.activeStep--
            }
        },
         handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        }
    }
}
</script>>
<style scoped>
.procss{
    background-color: rgb(248, 248, 248);
    padding-top: 40px
}
.stepOne{
    /* height: 1000px; */
    padding: 30px 40px
}
.oneLft{
    width: 48%;
    float: left;
}
.oneRight{
    width: 48%;
    float: right;
}
.uploadBox{
    width: 48%;
    float: left;
}
.uploadBoxt{
    width: 48%;
    float: right;
}
.huanzhe{
    width: 100%;
    float: left;
    border-top: 1px solid #ccc;
    padding-top: 30px
}
.twoLeft{
    width: 50%
}
.stepSi{
    width: 100%
}
</style>
<style>
.el-step.is-horizontal .el-step__line{
    top: 40px;
}
.el-step__icon{
    width: 80px;
    height: 80px;
}
.el-step__head.is-process .el-step__icon.is-text{
    border: 1px solid  rgb(71, 116, 182);
    background-color: rgb(71, 116, 182);
    color: #fff
}
.el-select {
    width: 100% !important;
}
.el-date-editor.el-input, .el-date-editor.el-input__inner{
    width: 100% !important
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>


